import React, { Component } from 'react';
import ReadMoreReact from 'read-more-react';
import { animateScroll as scroll } from "react-scroll";
import Navigation from '../../components/Navigation/Navigation';
import Footer from '../../components/Footer/Footer';
import VoteModal from '../../components/VoteModal/VoteModal';
import style from './JS.module.css';

class JS extends Component {

    state = {
        showModal: false,
        items: [],
        id: ''
    }
    handleModal = () => {
        this.setState({ showModal: !this.state.showModal });
    }
    componentDidMount() {
        fetch("http://goiteens.club/gameofteens/projects.php?category=js")
            .then(data => data.json())
            .then(data => this.setState({ items: data }));
    }


    render() {

const {handleModal} = this;
const {items, id} = this.state;

        const scrollToTop = () => {
            scroll.scrollToTop();
        };

        const modalOnTop = (id) => {
            scrollToTop();
            handleModal();
            this.setState({id: id})
            
        }



        return (
            <div className={style.body}>
                <Navigation />

                {this.state.showModal && <button className={style.closeBtn} onClick={this.handleModal}></button>}
                { this.state.showModal && <VoteModal id={id} handleModal={this.handleModal}/> }
      
                




                <button onClick={scrollToTop} className={style.topBtn}></button>


                <h2 className={style.title}>JavaScript</h2>
                <p className={style.intro}>Функціональні веб-сайти, веб-застосунки та браузерні ігри;</p>
                <ul className={style.workList}>
                {items.map(item => 
                    
                    <li key={item.id} className={style.workItem}>
                        <div className={style.likeCounter}><span className={style.likeNumber}>{item.votes}</span></div>
                        <img src={item.photo || "#"} alt="" />
                        <h4>{item.name}</h4>
                        <p className={style.projectIntro}>
                            <ReadMoreReact text={item.description}
                                min={200}
                                ideal={201}
                                max={500}
                                readMoreText={"...Читати далі"} /></p>

                        <button  className={style.demo}><a target="_blank" rel="noopener noreferrer" href={item.link}>Демо версія</a> </button>
                        <button key={item.id} onClick={()=>modalOnTop(item.id)} className={style.voteBtn}>Голосувати</button>


                    </li>
                    )}

                </ul>

                <Footer />
            </div>
        )
    }
}
export default JS;
